import { Component } from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-modifica-user',
  templateUrl: 'modifica-user.html',
})
export class ModificaUserPage {

  public loading: boolean = true;

  items: any=[];
  items2: any=[];
    generated = '';
    permisom: any=[];
    idempresa:any;
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";
dpi:any;
  pingreso:boolean=false; 
  pconsulta:boolean=false;
  peliminacion:boolean=false;
  pcreacion:boolean=false;
  pactualizacion:boolean=false;
//private barcodeScanner:BarcodeScanner,
  constructor(public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController,
    public navParams: NavParams,
     public regService: Auth
 

    ) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value
       
    })
      

  }
  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }
  showFilter()     
  {


    let prompt = this.alertCtrl.create({
      title: 'Ingrese CUI/NOV/EMAIL/NO PASAPORTE para buscar a usuario',
    
      inputs: [
        {
          name: 'dpi'
        }
      ],
      buttons: [
        {
          text: 'Cancelar'
        },
        {
          text: 'Buscar',
          handler: todo => {
 
                if(todo){
                  this.dpi=todo.dpi
 
                  this.regService.getReg(todo.dpi + '/actualizauser/todos/' + this.idempresa,'personals').then((data) => {
 
                    this.items=data;
                  
                }, (err) => {
                  this.msj.showError3('Error',err.error);
                });
                 
 
                }
 
 
          }
        }
      ]
    });
 
    prompt.present();

  }


  pideregistros() { 
      let self = this;
      self.loading = true;
      this.regService.getReg('todos/todos/'+ this.idempresa,'modulos').then((data) => {
        this.items=data;
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })  
  });
  }
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }
  ionViewDidLoad() {
   console.log(this.navParams.get("permiso"));
   // this.pideregistros();
  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;   
    setTimeout(() => ev.complete(), 1000);
  }


  cambioclave(item : any) : void
   {

    let prompt = this.alertCtrl.create({
      title:'Cambio de Contraseña',
      message: '',
      inputs: [
        {
          name: 'clave1',value:'Aspi123@',
          placeholder: 'Ingrese Contraseña nueva'
       
          
        }
      ],
      buttons: [
        {
          text: 'Cancelar'
        },
        {
          text: 'Actualizar Contraseña',
          handler: todo => {
  
                if(todo){
  
                
                    let credentials = {
                      idempresa:this.idempresa,
                      email         	:  item.email  	,
                      password: todo.clave1,
                      bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:'Actualiza',accion:'Cambia clave'}
                    };
                  
                  
                    this.regService.createAPISINTOKEN(credentials,'/api/auth/register2').then((result) => {
                      this.msj.showError3('Exito','Operación realizada con exito.');
                   
                      
                    }, (err) => {
                      this.msj.showError3('Advertencia','ERROR al actualizar registro');
                  
                    });   
  

  
                }
  
  
          }
        }
      ]
    });
  
    prompt.present();
   }

  updateRecord(item : any,no) : void
   {
   
    
    let prompt = this.alertCtrl.create({
      title: 'Modifique datos',
      inputs: [  {  name: 'email' , value:item.email },{  name: 'dpi' , value:item.cui }
      , {  name: 'nombre' , value:item.nombre } , {  name: 'telefono' , value:item.telefono },
      {  name: 'direccion' , value:item.direccion },  {  name: 'nov' , value:item.nov },
      {  name: 'estadoemail' , value:item.estadoemail }   ],
      buttons: [
        {
          text: 'Cancelar'
        },
        {
          text: 'Actualizar',
          handler: todo => {
    
                if(todo){

                  let   options       : any	     = { 
                    _id:item._id,
                    email: todo.email,
                    nombre        	: todo.nombre     	,
                    cui 	: todo.dpi 	,
                    direccion   	: todo.direccion   	,
                    telefono    	: todo.telefono    	,
                    nov    	: todo.nov    	,
                    estadoemail:todo.estadoemail,
                    bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:'Actualiza',accion:'actualiza usuario '}
                  };
      
                     
    
                //  this.regService.createReg(this._ID,options,'personals').then((result) => {
                  this.regService.createReg('actualiza2',options,'personals').then((result) => {

                
                    this.regService.getReg(this.dpi + '/actualizauser/todos/' + this.idempresa,'personals').then((data) => {
 
                      this.items=data;
                    
                  }, (err) => {
                    this.msj.showError3('Error',err.error);
                  });
                   
                  
                }, (err) => {
                  this.msj.showError3('Error',err.error);
                });
                   console.log(todo);
 
                }
 
 
          }
        }
      ]
    });
 
    prompt.present();

    
   }
 


}



